//base
import Vue from 'vue'
import Vuetify from 'vuetify'
import MuseUI from 'muse-ui'

//data
import router from './router.js' // vue-router
import App from './App'
import Helpers from 'muse-ui/lib/Helpers'
//import
import '@babel/polyfill' // vuetify <icon>
import 'vuetify/dist/vuetify.min.css' // vuetufy  css 
import 'muse-ui/dist/muse-ui.css' // muse-ui  css 
import '../style/pretty.scss' // scss
import '../style/common.scss' // scss
import '../style/style.scss' // scss

Vue.use(MuseUI)
Vue.use(Helpers)
Vue.use(Vuetify)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
